// src/components/MainNavbar.tsx
'use client';

import { ThemeProvider, Navbar, NavbarBrand, NavbarToggle, NavbarCollapse, NavbarLink } from 'flowbite-react';
import { navTheme } from '../theme/navbarTheme';
import { usePathname } from 'next/navigation';
import Image from 'next/image';
import Link from 'next/link';

export default function MainNavbar() {
  const pathname = usePathname();

  return (
    <>
      <div className="w-full h-[6px] bg-[#ef4653]" />
      <ThemeProvider theme={{ navbar: navTheme }}>
        <Navbar fluid rounded={false} className="border-b border-gray-200 bg-white shadow-sm">
          <NavbarBrand as={Link} href="/">
            <Image src="/logomain.png" alt="logo" width={120} height={32} priority />
          </NavbarBrand>
          <NavbarToggle />
          <NavbarCollapse className="gap-6">
            <NavbarLink as={Link} href="/" active={pathname === '/'}>หน้าหลัก</NavbarLink>
            <NavbarLink as={Link} href="/professor/dashboard" active={pathname.startsWith('/professor/dashboard')}>กิจกรรมของฉัน</NavbarLink>
            <NavbarLink as={Link} href="/registration" active={pathname.startsWith('/registration')}>ลงทะเบียนกิจกรรม</NavbarLink>
          </NavbarCollapse>
        </Navbar>
      </ThemeProvider>
    </>
  );
}
